						this.usernamer = response.data.username
						//console.log(this.usernamer, "loveyou")
						this.$store.commit('setUser', {'id':response.data.id, 'username':response.data.username})
						
						localStorage.setItem('username', response.data.username)
						localStorage.setItem('id', response.data.id)
						
						//console.log(this.$store.state.user)

					}).catch(error => {
						if(error.response){
							//console.log(error.response.data)
						}
				})
		},
		
		login(){
			axios.defaults.headers.common['Authorization'] = ''
			localStorage.removeItem('token')
				const fromData = {
					username: this.username,
					password: this.password,
					
					
				}
				
				axios
					.post('/api/v1/token/login/', fromData).then(response =>{
						
						const token = response.data.auth_token
						this.$store.state.hidetoken = response.data.auth_token
						this.$store.commit('setToken', token)
						this.loggedin = true
						axios.defaults.headers.common['Authorization'] = 'Token '+token

						localStorage.setItem('token', token)
						this.regis = true
						this.GETME()

						if(this.$route.params.type == 'item'){
							

							var self = this;
						
							setTimeout(function() {
								self.$router.push({ path: `/item/${self.$route.params.id}` })
							}, 1000);

						}else{
							var self = this;
							setTimeout(function() {
								self.$router.push({ path: '/home' })
							}, 1000);
						}

						
						//console.log(this.$route.params, 'toke')
						
						
						
						//document.getElementById("form").style.visibility = "hidden"
						//document.getElementById("formcred").style.visibility = "hidden"

