						}
					})
		},

		register(){
			
				if(this.password == this.secondpassword){
				const fromData = {
					username: this.username,
					password: this.password,
					email: this.email
				}

				axios
					.post('/api/v1/users/', fromData).then(response =>{
								
								this.escape = true
								this.password = ''
								//console.log("account was created please log in")
								this.terms = false

								var self = this;
						
								setTimeout(function() {
